<template>
	<div class="components-media-warp">
		<Amap></Amap>
		<balanceState ref="mediafixed1"></balanceState>
		<sewageBalance ref="mediafixed2"></sewageBalance>
		<surfaceWater @fullLoacl="fullLoacl($event)" ref="mediafixed3"></surfaceWater>
		<balanceReport ref="mediafixed4"></balanceReport>
		<a-icon v-show="fullShow" class="componets-media-full" :class="{location: islocaled}"
			:style="{fontSize: '35px',color: '#fff'}" :type="isFull ? 'fullscreen-exit' : 'fullscreen'"
			@click="full" />
	</div>
</template>

<script>
	import Amap from "@/components/media/balanceMap.vue"
	import balanceState from '@/components/media/waterBalance/balanceState.vue'
	import sewageBalance from '@/components/media/waterBalance/sewageBalance.vue'
	import surfaceWater from '@/components/media/waterBalance/surfaceWater.vue'
	import balanceReport from '@/components/media/waterBalance/balanceReport.vue'
	
	export default {
		components: {
			'Amap': Amap,
			'balanceState': balanceState,
			'sewageBalance': sewageBalance,
			'surfaceWater': surfaceWater,
			'balanceReport': balanceReport
		},
		data() {
			return {
				isFull: false,
				fullShow: true,
				islocaled: false,
			};
		},
		created() {
		},
		methods: {
			full() {
				this.isFull = !this.isFull
	
				if (this.isFull) {
					this.$refs.mediafixed1.myMoveIn()
					this.$refs.mediafixed2.myMoveIn()
					this.$refs.mediafixed3.myMoveIn()
					this.$refs.mediafixed4.myMoveIn()
				} else {
					this.$refs.mediafixed1.myMoveOut()
					this.$refs.mediafixed2.myMoveOut()
					this.$refs.mediafixed3.myMoveOut()
					this.$refs.mediafixed4.myMoveOut()
				}
			},
			fullLoacl(e) {
				this.fullShow = false
				this.isFull = e
				this.islocaled = e
				setTimeout(() => {
					this.fullShow = true
				}, 1000)
			}
		}
	};
</script>

<style scoped lang="less">
	.components-media-warp {
		position: relative;
		width: 100%;
		height: calc(100% - 117px);
		overflow: hidden;
	}
	
	.componets-media-full {
		position: absolute;
		bottom: 350px;
		right: 530px;
		z-index: 100;
		cursor: pointer;
	
		&.location {
			bottom: 50px;
			right: 50px;
		}
	}
</style>
