<template>
	<div class="media-warp" :style="{right: isMove ? '-505px' : ''}">
		<div class="media-warp-title" @click="myMoveIn">排水量统计</div>
		<div class="media-warp-title active" :style="{opacity: opacity}" v-show="isMove" @click="myMoveOut">
			排水量统计
		</div>
		<div class="media-warp-chart">
			<div class="title">当月排水量前十名</div>
			<div class="table">
				<div class="table-thead">
					<div class="table-th index">序号</div>
					<div class="table-th name">企业名称</div>
					<div class="table-th number">排放量（吨）</div>
				</div>
				<div class="table-tbody">
					<div class="table-tr" v-for="(item,index) in list">
						<div class="table-td index" v-if="index < 10">{{index + 1}}</div>
						<div class="table-td name">{{item.companyName}}</div>
						<div class="table-td number">{{item.waterQuantity}}</div>
					</div>
					<div class="table-tr" v-for="n in (10 - list.length)">
						<div class="table-td index"></div>
						<div class="table-td name"></div>
						<div class="table-td number"></div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		monthWaterQuantity
	} from '@/request/api'

	export default ({
		props: {},
		data() {
			return {
				isMove: false,
				opacity: '',
				list: []
			}
		},
		created() {},
		methods: {
			myMoveIn() {
				this.isMove = true;
				this.$emit('fullLoacl', true)
				setTimeout(() => {
					this.opacity = 1
				}, 1000)
			},
			myMoveOut() {
				this.opacity = 0
				this.$emit('fullLoacl', false)
				setTimeout(() => {
					this.isMove = false
				}, 250)
			},
		},
		mounted() {
			monthWaterQuantity({
				regionID: global.regionID,
			}).then(res => {
				if (res.status === '0') {
					console.log(res)
					this.list = res.result
				} else {
					console.log(res.message)
				}
			})
		}
	})
</script>

<style scoped lang="less">
	.media-warp {
		position: absolute;
		right: 20px;
		top: 10px;
		width: 490px;
		height: 460px;
		background: url(../../../../static/image/drainage-state.png) no-repeat left bottom / 100% 443px;
		transition: right 1s;
		-webkit-transition: right 1s;

		&-title {
			width: 238px;
			height: 40px;
			margin: 0 auto;
			background: url(../../../../static/image/fixed1-headr.png) no-repeat center center / 100% 100%;
			font-size: 18px;
			line-height: 40px;
			color: #49b6db;
			text-align: center;
			cursor: pointer;

			&.active {
				height: 238px;
				width: 40px;
				background: url(../../../../static/image/fixed1-headr-active.png) no-repeat center center / 100% 100%;
				color: #49b6db;
				text-align: center;
				writing-mode: vertical-lr;
				position: absolute;
				left: -45px;
				top: 0;
				bottom: 0;
				opacity: 0;
				transition: opacity 0.5s;
				-webkit-transition: opacity 0.5s;
				margin: auto;
			}
		}

		&-chart {
			height: calc(100% - 40px);
			position: relative;
			padding: 20px 0 0 0;
			
			.title{
				color: #02d9fd;
				padding-left: 12px;
			}

			.table {
				margin-top: 14px;
				color: #fff;
			
				&-thead {
					display: flex;
					background-color: rgba(9, 46, 152, 0.5);
				}
			
				&-tr {
					display: flex;
			
					&:nth-child(even) {
						background-color: rgba(9, 46, 152, 0.5);
					}
				}
			
				&-th,
				&-td {
					height: 30px;
					line-height: 30px;
					text-align: center;
				}
			
				.index {
					width: 50px;
				}
			
				.name {
					flex: 1;
					text-align: left;
					padding-left: 20px;
				}
			
				.number {
					width: 110px;
				}
			}
		}
	}
</style>
